import React, { useState, useEffect } from 'react';
import { artistService } from '../services/artistService';
import './ArtistManager.css';

const ArtistManager = ({ defaultOpen = false }) => {
  const [isOpen, setIsOpen] = useState(defaultOpen);
  const [selectedArtist, setSelectedArtist] = useState(null);
  const [artists, setArtists] = useState([]);
  const [formData, setFormData] = useState({
    name: '',
    city: '',
    country: '',
    lat: '',
    lng: '',
    style: '',
    specialties: ['', '', ''],
    avatar: '',
    color: '#000000',
    instagram: '',
    website: '',
    bio: '',
    awards: ['', '']
  });

  // 
  useEffect(() => {
    const loadedArtists = artistService.getArtists();
    setArtists(loadedArtists);
  }, []);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    if (name.startsWith('specialties[')) {
      const index = parseInt(name.match(/\[(\d+)\]/)[1]);
      setFormData(prev => ({
        ...prev,
        specialties: prev.specialties.map((s, i) => i === index ? value : s)
      }));
    } else if (name.startsWith('awards[')) {
      const index = parseInt(name.match(/\[(\d+)\]/)[1]);
      setFormData(prev => ({
        ...prev,
        awards: prev.awards.map((a, i) => i === index ? value : a)
      }));
    } else {
      setFormData(prev => ({
        ...prev,
        [name]: value
      }));
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const cleanedFormData = {
      ...formData,
      specialties: formData.specialties.filter(s => s.trim() !== ''),
      awards: formData.awards.filter(a => a.trim() !== '')
    };

    if (selectedArtist) {
      const updatedArtists = artistService.updateArtist(selectedArtist.id, cleanedFormData);
      setArtists(updatedArtists);
    } else {
      const updatedArtists = artistService.addArtist(cleanedFormData);
      setArtists(updatedArtists);
    }
    
    setSelectedArtist(null);
    setFormData({
      name: '',
      city: '',
      country: '',
      lat: '',
      lng: '',
      style: '',
      specialties: ['', '', ''],
      avatar: '',
      color: '#000000',
      instagram: '',
      website: '',
      bio: '',
      awards: ['', '']
    });
  };

  const handleEdit = (artist) => {
    setSelectedArtist(artist);
    setFormData({
      ...artist,
      specialties: [
        ...artist.specialties,
        ...Array(3 - artist.specialties.length).fill('')
      ],
      awards: [
        ...artist.awards,
        ...Array(2 - artist.awards.length).fill('')
      ]
    });
    setIsOpen(true);
  };

  return (
    <div className="artist-manager">
      <button 
        onClick={() => setIsOpen(!isOpen)}
        className="toggle-button"
      >
        {isOpen ? '' : ''}
      </button>

      {isOpen && (
        <div className="manager-panel">
          <h2>{selectedArtist ? '' : ''}</h2>
          <form onSubmit={handleSubmit}>
            <div className="form-group">
              <label>:</label>
              <input
                type="text"
                name="name"
                value={formData.name}
                onChange={handleInputChange}
                required
              />
            </div>

            <div className="form-group">
              <label>:</label>
              <input
                type="text"
                name="city"
                value={formData.city}
                onChange={handleInputChange}
              />
            </div>

            <div className="form-group">
              <label>:</label>
              <input
                type="text"
                name="country"
                value={formData.country}
                onChange={handleInputChange}
                required
              />
            </div>

            <div className="form-group">
              <label>:</label>
              <input
                type="number"
                name="lat"
                value={formData.lat}
                onChange={handleInputChange}
                required
                step="any"
              />
            </div>

            <div className="form-group">
              <label>:</label>
              <input
                type="number"
                name="lng"
                value={formData.lng}
                onChange={handleInputChange}
                required
                step="any"
              />
            </div>

            <div className="form-group">
              <label>:</label>
              <input
                type="text"
                name="style"
                value={formData.style}
                onChange={handleInputChange}
                required
              />
            </div>

            <div className="form-group">
              <label>:</label>
              {formData.specialties.map((specialty, index) => (
                <input
                  key={index}
                  type="text"
                  name={`specialties[${index}]`}
                  value={specialty}
                  onChange={handleInputChange}
                  placeholder={` ${index + 1}`}
                />
              ))}
            </div>

            <div className="form-group">
              <label>URL:</label>
              <input
                type="text"
                name="avatar"
                value={formData.avatar}
                onChange={handleInputChange}
                required
              />
            </div>

            <div className="form-group">
              <label>:</label>
              <input
                type="color"
                name="color"
                value={formData.color}
                onChange={handleInputChange}
                required
              />
            </div>

            <div className="form-group">
              <label>Instagram:</label>
              <input
                type="text"
                name="instagram"
                value={formData.instagram}
                onChange={handleInputChange}
              />
            </div>

            <div className="form-group">
              <label>:</label>
              <input
                type="text"
                name="website"
                value={formData.website}
                onChange={handleInputChange}
              />
            </div>

            <div className="form-group">
              <label>:</label>
              <textarea
                name="bio"
                value={formData.bio}
                onChange={handleInputChange}
                required
              />
            </div>

            <div className="form-group">
              <label>:</label>
              {formData.awards.map((award, index) => (
                <input
                  key={index}
                  type="text"
                  name={`awards[${index}]`}
                  value={award}
                  onChange={handleInputChange}
                  placeholder={` ${index + 1}`}
                />
              ))}
            </div>

            <button type="submit">
              {selectedArtist ? '' : ''}
            </button>
            {selectedArtist && (
              <button 
                type="button" 
                onClick={() => {
                  setSelectedArtist(null);
                  setFormData({
                    name: '',
                    city: '',
                    country: '',
                    lat: '',
                    lng: '',
                    style: '',
                    specialties: ['', '', ''],
                    avatar: '',
                    color: '#000000',
                    instagram: '',
                    website: '',
                    bio: '',
                    awards: ['', '']
                  });
                }}
              >
                
              </button>
            )}
          </form>

          <div className="artists-list">
            <h3></h3>
            {artists.map(artist => (
              <div key={artist.id} className="artist-item">
                <img src={artist.avatar} alt={artist.name} />
                <div className="artist-info">
                  <h4>{artist.name}</h4>
                  <p>{artist.country}</p>
                </div>
                <button onClick={() => handleEdit(artist)}></button>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

export default ArtistManager;
